<template>
    <div class="blogAdd">
        <div class="addMain">
            <div class="row">
                <div class="text">博客标题：</div>
                <div class="iptBox">
                    <input type="text" placeholder="请输入标题" v-model="blogObj.title"/>
                </div>
            </div>
            <div class="row">
                <div class="text">作者：</div>
                <div class="iptBox iptBoxAuthor">
                    <input type="text" placeholder="请输入作者姓名" v-model="blogObj.author"/>
                </div>
            </div>
            <div class="row">
                <div class="text">博客分类：</div>
                <div class="iptBox">
                    <select v-model="blogObj.classify">
                        <option :value="item.classname" v-for="item in classArr" :key="item.id">{{item.classname}}</option>
                    </select>
                </div>
            </div>
            <div class="row rowArea">
                <div class="text">主题内容：</div>
                <div class="iptBox">
                    <textarea v-model="blogObj.content"></textarea>
                </div>
            </div>
            <div class="row">
                <div class="text"></div>
                <div class="iptBox">
                    <button @click="onSubmit">提交</button>
                </div>
            </div>

        </div>
        <div class="addShow" v-if="addShow">
            <h2>博客预览</h2>
            <BlogItem :itemObj="blogObj"></BlogItem>
        </div>
    </div>
</template>
<script>
import BlogItem from './BlogItem'
export default {
  data () {
    return {
      blogObj: {
        title: '',
        posttime: new Date().getTime() / 1000,
        classify: '',
        author: '',
        content: ''
      },
      classArr: []
    }
  },
  components: {
    BlogItem
  },
  computed: {
    addShow () {
      if (this.blogObj.title || this.blogObj.classify || this.blogObj.author || this.blogObj.content) {
        return true
      } else {
        return false
      }
    }
  },
  mounted () {
    this.axios.get('https://ku.qingnian8.com/dataApi/blog/classBlog.php').then(res => {
      console.log(res.data)
      this.classArr = res.data
    })
  },
  methods: {
    onSubmit () {
    //   for (var item in this.blogObj) {
    //     if (this.blogObj[item] == '') {
    //       alert('请按要求填写')
    //     }
    //     return
    //   }

      this.axios.get('https://ku.qingnian8.com/dataApi/blog/addBlog.php', {
        params: this.blogObj
      }).then(res => {
        if (res.data.code == 200) {
          alert('发布成功')
          this.$router.push('/')
        };
      })
    }
  }
}
</script>
