<template>
    <div class="blogShow">
        <BlogItem v-for = "(item,idx) in arrList" :key="idx" :itemObj="item"></BlogItem>
        <div class="btnGroup">
            <button @click="prev">&lt; 上一页</button>
            <button @click="next">下一页 &gt;</button>
        </div>
    </div>
</template>
<script>
import BlogItem from './BlogItem'
export default {
  data () {
    return {
      arrList: [],
      pageNum: 2
    }
  },
  components: {
    BlogItem
  },
  created () {

  },
  mounted () {
    this.getData()
  },
  methods: {
    getData (p) {
      this.axios.get('https://ku.qingnian8.com/dataApi/blog/showBlog.php', {
        params: {
          page: p
        }
      }).then(res => {
        console.log(res.data)
        this.arrList = res.data
        if (document.body.scrollTop) {
          document.body.scrollTop = 0
        } else {
          document.documentElement.scrollTop = 0
        }
      })
    },
    prev () {
      if (this.pageNum <= 1) {
        this.pageNum = 1
      } else {
        this.pageNum--
        this.getData(this.pageNum)
      }
    },
    next () {
      if (this.arrList.length < 9 || this.arrList.length == 0) {
        alert('没有更多数据了')
        return
      }
      this.pageNum++
      this.getData(this.pageNum)
    }
  }
}
</script>
<style lang="less" scoped>
@mainColor:#41b883;
.blogShow{
    padding:50px 30px;
}
.btnGroup{
    padding:20px 0;
    display: flex;
    justify-content: center;
    button{
        border:1px solid @mainColor;
        background: #fff;
        color: @mainColor;
        font-size: 18px;
        border-radius: 3px;
        padding:5px 10px;
        margin:0 3px;
        outline: none;
        cursor: pointer;
        transition: .3s;
        &:hover{
            background: @mainColor;
            color: #fff;
        }
    }
}

</style>
